import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

export const router = new VueRouter({
    base: process.env.BASE_URL,
    mode: 'history',
    routes: [
        {
            path: '/',
            name: "HomePage",
            component: () => import("@/views/HomePage"),
            meta: {
                showBottomAdditional: true,
            },
        },
        {
            path: '/products',
            name: "ProductsPage",
            component: () => import("@/views/ProductsPage"),
        },
        {
            path: '/products/kass',
            name: "ProductPage",
            component: () => import("@/views/ProductPage"),
        },
        {
            path: '/news',
            name: "NewsPage",
            component: () => import("@/views/NewsPage"),
        },
        {
            path: '/news/kass',
            name: "NewsItemPage",
            component: () => import("@/views/NewsItemPage"),
        },
        {
            path: '/contacts',
            name: "ContactsPage",
            component: () => import("@/views/ContactsPage"),
        },
    ],
})