<template>
  <div class="reg-complect-item__row">
    <ins
      :style="`background-image: url(${iconSrc})`"
    ></ins>
    <div class="item-row__title">
      <span>{{ title }}</span>
    </div>
    <span class="item-row__price">{{ price }}</span>
  </div>
</template>

<script>
const iconMap = {
  "check": require("@/assets/check.svg"),
  "uncheck": require("@/assets/uncheck.svg"),
};

export default {
  name: "RegComplectRow",

  props: {
    title: String,
    price: String,
    include: {
      type: Boolean,
      required: true,
    },
  },

  computed: {
    iconSrc() {
      return this.include === true ? iconMap["check"] : iconMap["uncheck"];
    },
  },
}
</script>

